import React, { useState } from "react";
import Class from "./class";
import Function from "./function";

function Done() {
  const [displayf, notodisplayf] = useState(false);
  const [displayc, notodisplayc] = useState(false);

  return (
    <div>
      <button className="tosee" onClick={() => notodisplayf(!displayf)}>
        To style using functional component
      </button>
      <button className="tosee" onClick={() => notodisplayc(!displayc)}>
        To style using class component
      </button>
      <br />
      {displayf && <Function />}
      {displayc && <Class />}
    </div>
  );
}

export default Done;
